import React, { useState } from "react";
import { Button } from "react-bootstrap";
import PaymentCheckout from "../PaymentCheckout";

function Layout({ subscriptionPlansList }) {
  const [buyPlan, setBuyPlan] = useState();
  const [paymentCheckoutModel, setPaymentCheckoutModel] = useState(false);

  return (
    <div className="patient__prescription-forms custom-plans">
      {subscriptionPlansList?.map((item, index) => {
        return (
          <div className="container" key={`id-${index}`}>
            <h3 className="title mt-2 fw-bold">{item?.name}</h3>
            <h3 className="price">
            €{item?.amount}
              <span> / {item?.duration}</span>
            </h3>
            <p className="description">{item?.description}</p>
            <p className="m-0">
              <b>No. of Appointment(s):</b> {item?.numberOfConsultation}
            </p>
            <p className="m-0">
              <b>No. of Prescription(s):</b> {item?.numberOfPrescriptions}
            </p>
            <Button
              className="subscribe-button"
              onClick={() => {
                setPaymentCheckoutModel(true);
                setBuyPlan(item);
              }}
            >
              Buy Now
            </Button>
          </div>
        );
      })}
      <PaymentCheckout
        planData={buyPlan}
        handleClose={() => setPaymentCheckoutModel(false)}
        show={paymentCheckoutModel}
      />
    </div>
  );
}

export default Layout;
