import React, { useEffect } from "react";
import { Card } from "react-bootstrap";
import { Link } from "react-router-dom";
import { IoIosArrowForward } from "react-icons/io";
import { PATH } from "../../../../utils/path";
import Layout from "./CardsLayout";
import { useDispatch, useSelector } from "react-redux";
import { getAllSubscriptionPlans } from "../../../../app/features/subscriptionPlans/subscriptionPlans.slice";
import FullScreenLoader from "../../../../authorization/FullScreenLoader";

export default function MyPlans() {
  const { subscriptionPlansList, isLoading, isError, isSuccess } = useSelector(
    (state) => state?.subscriptionPlans
  );
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getAllSubscriptionPlans());
  }, [dispatch]);

  return (
    <>
      <nav aria-label="breadcrumb">
        <ol className="breadcrumb">
          <li className="breadcrumb-item">
            <Link
              to={PATH.PATIENT_PLANS}
              className="text-decoration-none fs-5 color-99"
            >
              My Plans
            </Link>
          </li>
          <IoIosArrowForward className="mx-1 mt-2 color-99" />
          <li
            className="breadcrumb-item active fs-5"
            aria-current="page"
            style={{ color: "#000071" }}
          >
            Subscription Plans
          </li>
        </ol>
      </nav>
      <Card className="shadow-sm p-4">
        <div>
          {isLoading ? (
            <FullScreenLoader />
          ) : isSuccess ? (
            subscriptionPlansList?.length > 0 ? (
              <Layout subscriptionPlansList={subscriptionPlansList} />
            ) : (
              <p className="record-message">No Records to Display</p>
            )
          ) : isError ? (
            <span className="text-danger fst-italic">Network Error</span>
          ) : null}
        </div>
      </Card>
    </>
  );
}
